<script>
export default {
  data() {
    return {
      marDec: [],
      comDec: [],
      premiumWait: [],
      premiumUser: [],
    };
  },
  beforeCreate() {
    if (this.$session.get("coolUserNo") == null) {
      this.$router.push("/Login");
    } else if (this.$session.get("coolRank") != 0) {
      this.$router.push("MyPage");
    }
  },
  mounted() {
    this.$axios
      .get("http://localhost:9000/managerDataList")
      .then((res) => {
        this.marDec = res.data.marDec;
        this.comDec = res.data.comDec;
        this.premiumWait = res.data.premiumWait;
        this.premiumUser = res.data.premiumUser;
      })
      .catch((err) => {
        console.log(err);
      });
  },
  methods: {
    logOut() {
      this.$session.clear();
      alert(" .");
      this.$router.push("/");
    },
    CommuDetail(num) {
      this.$router.push({ name: "CommuDetail", query: { comNo: num } });
    },
    MarketDetail(num) {
      this.$router.push({ name: "MarketDetail", query: { marNo: num } });
    },
    marAllDel() {
      this.$axios
        .get("http://localhost:9000/marAllDel")
        .then(() => {
          alert(" ");
          this.$router.go();
        })
        .catch((err) => {
          console.log(err);
        });
    },
    comAllDel() {
      this.$axios
        .get("http://localhost:9000/comAllDel")
        .then(() => {
          alert(" ");
          this.$router.go();
        })
        .catch((err) => {
          console.log(err);
        });
    },
    preAllUp() {
      let userNoList = [];
      for (let i = 0; i < this.premiumWait.length; i++) {
        userNoList.push(this.premiumWait[i].userNo);
      }
      this.$axios
        .get("http://localhost:9000/preAllUp", {
          params: { userNoList: userNoList },
        })
        .then(() => {
          alert(" ");
          this.$router.go();
        })
        .catch((err) => {
          console.log(err);
        });
    },
    preUp(num) {
      this.$axios
        .get("http://localhost:9000/preUp", { params: { userNo: num } })
        .then(() => {
          alert(" ");
          this.$router.go();
        })
        .catch((err) => {
          console.log(err);
        });
    },
    won(num) {
      if (num != null) {
        return num.replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ",");
      }
    },
    path(str) {
      return "../../../../" + str;
    },
  },
};
</script>
